import { Injectable } from '@angular/core';
import { Subject ,  Observable } from 'rxjs';
import { ComponentData } from '@verdocs/sdk';
import { find } from 'lodash';
import { MatDialogRef } from '@angular/material/dialog';

@Injectable()
export class ModalService {
  private modals: MatDialogRef<any>[] = [];
  private componentData: any;
  private subject: Subject<ComponentData> = new Subject<ComponentData>();

  add(modal: any) {
    this.modals.push(modal);
  }

  open(id: string) {
    const modal = find(this.modals, { id: id });
  }

  close(id: string) {
    const modal = find(this.modals, { id: id });
    modal.close();
  }

  setComponentData(componentData: any) {
    this.componentData = componentData;
    this.subject.next(componentData);
  }

  getComponentData(): Observable<any> {
    return this.subject.asObservable();
  }
}
